<template>
    <div class="banner swiper-container">
        <div v-if="banner == 'home'" class="home swiper-wrapper">
            <div class="swiper-slide"><a><img src="../assets/img/banner/banner01.jpg"/></a></div>
            <div class="swiper-slide"><a><img src="../assets/img/banner/banner02.jpg"/></a></div>
        </div>
        <div v-else class="server swiper-wrapper">
            <div class="swiper-slide"><a><img src="../assets/img/banner/banner03.jpg"/></a></div>
            <div class="swiper-slide"><a><img src="../assets/img/banner/banner04.jpg"/></a></div>
        </div>
        <div class="swiper-pagination">
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    // import {slider} from '../assets/js/slider'
    import {st} from '../const'
    import Swiper from '../assets/js/swiper-3.4.0.min';

    export default{
        props: {
            banner: {type: String, default: 'home'}
        },
        ready(){
            let vm = this;
            this.$nextTick(function(){
                var mySwiper = new Swiper ('.swiper-container.banner', {
                   loop: true,
                   autoplay: 5000,
                   autoplayDisableOnInteraction : false,
                   // 如果需要分页器
                   pagination: '.swiper-pagination',
                   paginationType : 'bullets'
                })
            });
            setTimeout(
                    function () {
                        vm.$dispatch('banner-ready');
                    }, 50);
        }
        ,
        data()
        {
            return {
                banners: []
            }
        },
        computed: {
            length(){
                return this.banners.length;
            }
        }
    }
</script>
<style scoped>
    .banner div a {
        display: block;
    }

    .banner div img {
        width: 100%;
        display: block;
    }
</style>



