<template>
  <div class="home-menu swiper-container">
    <div class="parent swiper-wrapper">
      <div  v-for="group in groups" class="swiper-slide" style="background-color:#fff;">
        <ul class="child">
          <li v-for="menu in group.menus"
           :class="[$index < 3 ? 'above':'below',($index % 3 == 0 ||$index % 3 == 1) ? 'r-line':'']">
            <a v-if="menu.name != ''" @click="linkTo(menu);">
               <!-- {name:'service-list',query:{id:menu.serviceId,name:menu.name}} -->
              <span class="img" style="background-image:url({{menu.icon | img-host}})"></span>
              <span>{{menu.name}}</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <div class="swiper-pagination" style="position:static;">
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import {url} from '../const'
  import {slider} from '../assets/js/slider'

  export default{
    ready(){
      this.$on('reload-child', this.init);
      this.$emit('reload-child');
    },
    data(){
      return {
        groups: [{menus: []}],
        pageIndex: 0,
        prevent:false//是否阻止点击
      }
    },
    methods: {
      linkTo(menu){
        // console.log('被点击了');
        this.$nextTick(function(){
          if(this.prevent == true){
            return;
          }
          window.location.href = `#/service/list?id=${menu.serviceId}&name=${menu.name}&attes=1`;
        })
      },
      init(){
        this.$http.get(url.hotCategory).then(
          function (response) {
            this.groups =[{menus: []}];
            let data = response.data.data;
            let group = data.length % 6 == 0 ? data.length / 6 : (data.length / 6 + 1);
            group = parseInt(group);
            let menus;
            let groups = [];
            for (let i = 0; i < group; i++) {
              menus = [];
              for (let j = i * 6; j < (i * 6 + 6); j++) {
                if (data[j]) {
                  if (!data[j].icon) {
                    data[j].icon = '/menu.png';
                  }
                  menus.push(data[j]);
                } else {
                  menus.push({name: ''});
                }
              }
              groups.push({menus: menus});
            }
            this.groups = groups;
            this.$nextTick(function(){
              this.loadBox();
            });
            this.$dispatch('load-back',1);
          },function(response){
            this.$dispatch('load-back',-1);//加载失败
          }
        );
      },
      loadBox(){
        let vm = this;
        var mySwiper = new Swiper ('.swiper-container.home-menu', {
           loop: false,
           // 如果需要分页器
           pagination: '.swiper-pagination',
           paginationType : 'bullets',
           onTouchEnd: function(swiper,event){
             let touches = swiper.touches;
             let startX = touches.startX;
             let currX = touches.currentX;
             let diff = startX - currX;
             if(Math.abs(diff) <= 3){
               vm.prevent = false;
             }else{
               vm.prevent = true;
             }
             return true;
            //  console.log('touch-end:',JSON.stringify(swiper.touches));
           }
        })
      }
    }
  }
</script>

<style scoped>
  ul.child li {
    float: left;
    text-align: center;
    width: 33.33%;
  }

  .child li.below {
    border-top: .01rem solid #f2f2f2;
  }

  .child li.above a{
    margin-bottom: .2rem;
    padding-top: .25rem;
  }

  .child li.below a{
    margin-top: .25rem;
    padding-bottom: .2rem;
  }

  .child li.r-line a{
    border-right: .01rem solid #f2f2f2;
  }

  ul.child li:last-child {
    border-right: none;
  }

  ul.child a {
    display: block;
    color: #666;
    font-size: 0.12rem;
    line-height: 1;
    vertical-align: middle;
  }

  ul.child span {
    display: block;
    font-size: .16rem;
    margin-top: .15rem;
    color: #333;
  }

  ul.child .img {
    width: 1rem;
    height: .83rem;
    background-size: 1rem;
    margin: .05rem auto;
    border-radius: 50%;
  }
</style>



