<template>
    <div>
        <v-header>
            <div slot="left" class="h-left">
                <a @click="_toast" style="margin-left:.1rem;">杭州<span class="arrow-down tip right"></span></a>
            </div>
            <div slot="right" class="h-right">
                <a v-link="{name:'home-server'}"><span class="toggle img"></span></a></div>
        </v-header>

        <v-content>
            <div id="scroll-warp">
                <div id="scroll-box">
                    <div id="pullDown">
                        <span class="pullDownLabel" v-show="isReady">下拉刷新…</span>
                        <span class="pullDownIcon"></span>
                    </div>
                    <v-banner :banner="'home'" id="banner"></v-banner>
                    <!-- <div class="search clear-fix" id="search">
                      <span class="img"></span>
                      <input type="text" v-link="{name:'search-keyword'}" class="keyword button-style"
                             placeholder="输入行业名称/匠人昵称" readonly="readonly">
                    </div> -->
                    <div class="top-menu" id="top-menu">
                        <div class="left mg-l10" style="text-indent:.2rem;" v-link="{name:'search-keyword'}">
                            <span class="search img"></span>搜索
                        </div>
                        <div class="title"><span class="img super-bull"></span></div>
                        <div class="right mg-r10" v-link="{name:'menu-all'}">全部分类</div>
                    </div>

                    <v-home-menu id="menu"></v-home-menu>

                    <v-service-list class="service-list recommend" :link_name="'recommend'">
                        <p slot="title" class="title">
                            <span>最受欢迎 TOP10</span>
                        </p>
                    </v-service-list>

                    <!-- <v-service-list :user_id="userId" class="service-list his" :link_name="'his'" v-if="userId > 0">
                      <p slot="title" class="title"><span class="img history"></span>点过的匠人</p>
                    </v-service-list> -->
                </div>
            </div>
        </v-content>

        <v-footer>
        </v-footer>
    </div>
</template>
<script>
    import VHeader from '../components/Header'
    import VContent from '../components/Content'
    import VFooter from '../components/Footer'
    import VBanner from '../components/Banner'
    import VHomeMenu from '../components/HomeMenu'
    import VServiceList from '../components/ServiceList'
    import {lt} from '../const'
    import $ from 'jquery'
    import {loading} from '../assets/js/iscroll'
    import {toast} from '../assets/js/toast'

    export default {
        route: {
            data({from, to}){
                if (this.isReady) {
                    this.$broadcast('reload-footer');
                    return {
                        state: !this.state
                    }
                }
            }
        },
        ready(){
            this.$on('ready', this.init);
            this.$emit('ready');
            this.isReady = true;
        },
        data () {
            return {
                isReady: false,
                state: false,

                userId: 0,
                hasLoad: 0,//已加载的组件
                isDown: false//下拉状态
            }
        },
        methods: {
            _toast(){
                !!joy && joy.toast('哈哈哈哈哈');
            },
            init(){
                if (lt.has(lt.keys.user)) {
                    const user = lt.getJson(lt.keys.user);
                    console.log('当前登录用户id:' + user.userId);
                    this.userId = user.userId;
                }
            },
            initScroll(){
                let vm = this;
                let name = "home";
                let hasIn = false;
                if (loading.hasScroll()) {
                    if (loading.getName() == name) {
                        hasIn = true;
                    } else {
                        loading.destroy();
                    }
                }
                if (!hasIn) {
                    loading.ready({
                        wrapper: 'scroll-warp',
                        down: 'pullDown',
                        downText: '.pullDownLabel',
                        downFn: vm.down,
                        name: name,
                        onlyRefs: true
                    });
                }
            },
            down(){
                this.hasLoad = 0;
                this.isDown = true;
                this.$broadcast('reload-child');
            }
        },
        events: {
            'load-back': function (state) {
                if (state == -1) {
                    if (this.isDown) {
                        loading.downError();
                        this.isDown = false;
                    } else {
                        toast.alert('网络异常');
                    }
                }
                this.hasLoad = this.hasLoad + 1;
            },
            'banner-ready': function () {
                $('#scroll-warp').css('top', '0px');
                this.initScroll();
            }
        },
        watch: {
            'state': function () {
                let vm = this;
                setTimeout(function () {
                    vm.initScroll();
                }, 100);
                let userId = 0;
                if (lt.has(lt.keys.user)) {
                    const user = lt.getJson(lt.keys.user);
                    console.log('当前登录用户id:' + user.userId);
                    userId = user.userId;
                }
                if (userId != vm.userId) {//用户id改变时 重新加载首页
                    vm.userId = userId;
                    loading.top();
                }
            },
            'hasLoad': function () {
                console.log(this.hasLoad);
                let loaded = false;
                if (this.hasLoad == 2) {
                    loaded = true;
                }
                if (loaded) {
                    let h = 0;
                    // if (this.userId == 0) {
                    h = $("#banner").height() + $("#top-menu").height() + 344 +
                        $(".service-list.recommend").height() + 20;
                    // } else {
                    //   h = $("#banner").height() + $("#search").height() + $("#menu").height() +
                    //           $(".service-list.recommend").height() + $(".service-list.his").height() + 20;
                    // }

                    // console.log('loaded h->', $("#top-menu").height(), $("#menu").height(), $(".service-list.recommend").height());
                    $("#scroll-box").css('height', h + "px");
                    if (this.isDown) {
                        loading.refs();
                        this.isDown = false;
                    }
                }
            }
        },
        components: {
            VHeader,
            VFooter,
            VBanner,
            VHomeMenu,
            VContent,
            VServiceList
        }
    }
</script>
<style scoped>
    #scroll-warp {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .arrow-down {
        background-image: url(../assets/img/top/arrow-down.png);
    }

    .arrow-down.tip {
        width: 0.09rem;
        height: .05rem;
        background-size: .09rem;
        margin: .2rem auto 0 .05rem;
        position: absolute;
    }

    .img.toggle {
        background-image: url(../assets/img/icon/i_change.png);
    }

    .content .top-menu {
        height: .45rem;
        width: 100%;
        position: relative;
        font-size: .12rem;
        color: #999;
        line-height: .4rem;
        margin-top: .08rem;
        overflow: hidden;
    }

    .top-menu .title {
        position: absolute;
        width: 100%;
        padding-top: 100%;
        top: 0;
        left: 50%;
        -webkit-transform: translateX(-50%);
        background: #fff;
        border-radius: 50%;
        z-index: -1;
    }

    .img.search {
        position: absolute;
        left: .1rem;
        top: .11rem;
        width: .14rem;
        height: .14rem;
        background-size: .14rem;
        background-image: url(../assets/img/icon/i_search.png);
    }

    .img.super-bull {
        position: absolute;
        top: .14rem;
        left: 50%;
        margin-left: -.36rem;
        height: .14rem;
        width: .72rem;
        background-size: .72rem;
        background-image: url(../assets/img/icon/title_pic.png);
    }

    /*.content .search {
      height: .6rem;
      width: 100%;
      background-color: #f4f4f4;
      padding: .1rem;
      position: relative;
    }

    .search .img {
      position: absolute;
      left: .2rem;
      top: .225rem;
      width: .15rem;
      height: .15rem;
      background-size: .15rem;
      background-image: url(../assets/img/icon/search.png);
    }

    .search .keyword {
      text-indent: .3rem;
      width: 100%;
      height: 100%;
    }*/

    .service-list .title {
        height: .5rem;
        background-color: #fff;
    }

    .service-list .title span {
        color: #333;
        border-left: .03rem solid #ff3e3e;
        font-weight: inherit;
        line-height: .5rem;
        padding: .03rem 0;
        padding-left: .1rem;
        font-size: .16rem;
    }

    .service-list .title .img {
        position: absolute;
        left: .1rem;
        top: .12rem;
        width: .16rem;
        height: .16rem;
        background-size: .16rem;
    }

    .service-list .recommend {
        background-image: url(../assets/img/icon/hot.png);
    }

    .service-list .history {
        background-image: url(../assets/img/icon/history.png);
    }
</style>



